* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
}

.calendar {
  gap: 1px;
  width: 726px;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;

  &__day {
    box-sizing: border-box;
    border: 1px solid #000;
    width: 100px;
    height: 100px;
    background-color: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 500ms;

    @for $day from 1 through 31 {
      &:nth-child(#{$day}) {
        &::before {
          content: "#{$day}";
        }
      }
    }

    &:hover {
      background-color: pink;
      cursor: pointer;
      transform: translateY(-20px);
    }

    $start-day-values:
      mon 6px,
      tue 106px,
      wed 206px,
      thu 306px,
      fri 406px,
      sat 506px,
      sun 606px;

    @each $day, $value in $start-day-values {
      .start-day--#{$day} :first-child {
        margin-left: 100px;
      }
    }
  }

  @for $calendar-length from 28 through 31 {
    .calendar-length--#{$calendar-length} {
      display: none;
    }
  }
} 
